import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { ConfirmationService, Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import * as _ from 'lodash';
import { RuleExeConfig, SalableProduct } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-exe-config/bean/rule-exe-config.bean";
import { ProductInterestRate } from "app/pages/tzb/valuation/product-interest-allocation/deposit-product-interest-rate/bean/product-interest-rate.bean";
import { checkEnAndNum, TwoInteger, specialSymbols, checkChinese } from "app/@uisftech/common/service/validator";

@Component({
    selector: 'rule-exe-config-update',
    templateUrl: './rule-exe-config-update.component.html',
    styleUrls: ['./rule-exe-config-update.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class RuleExeConfigUpdateComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    firsts: number = 0;
    pageSizes: number = 10;
    pageNums: number = 1;
    totals: number = 0;
    firstes: number = 0;
    pageSizees: number = 10;
    pageNumes: number = 1;
    totales: number = 0;
    display: boolean = false;//利率弹出框控制
    displays: boolean = false;//可售产品弹框控制
    isClick: boolean = false;//控制不可连续多次提交
    dataCode: any;//传入数据
    dataName: any;//传入数据
    title: any;//页面标题
    rateTypeNameAndBaseRateList: any = [];//利率弹出框列表
    conditionalDimensionList: any = [];//条件维度明细数据
    ProductCondList: any = [];//条件维度编号
    ProductValueList: any = [];//条件维度名称
    listOptions: any = [];//利率分类类型码值
    interestType: any = [];//利率种类码值
    ActionTypeList: any = [];//规则行为名称码值
    state: any = [];//状态码值
    chooseList: any = [];//选择条件维度存储
    chooseName: any = [];//选择条件维度名称存储
    dimensionList: any = [];//条件维度值码值
    rateTermList: any = [];//期限码值
    selectedValue: any;//利率单选数据
    EnAndNumShow: Boolean = false;//规则执行编号校验
    ChineseShow: Boolean = false;//规则执行名称校验
    numberShow: Boolean = false;//规则行为取值校验
    numberShow1: Boolean = false;//利率值校验
    raterateNameShow: Boolean = false;//利率名称校验
    orgTreeDisplay: Boolean = false;//机构树弹出框控制
    ownerOrgInValue: any;//机构树inValue
    rateOrgName: any;//机构号对应机构片区
    minDate: Date = new Date();//当前日期
    nextDate: Date;//当前日期的后一天
    productList: any = [];//可售产品列表
    productPriceCode: any;//产品编码
    productPriceName: any;//产品名称
    baseRateInterestList: any = [];//利率单位码值
    iswork: any;//是否是工作流
    rateName: any;//利率名称
    ruleexeconfig: RuleExeConfig = new RuleExeConfig();
    productinterestrate: ProductInterestRate = new ProductInterestRate();
    salableproduct: SalableProduct = new SalableProduct();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) { }

    ngOnInit() {
        this.nextDate = new Date(this.minDate.getTime() + 24 * 60 * 60 * 1000);//	当前日期的后一天
        this.title = this.route.snapshot.params['title'];
        this.dataCode = this.route.snapshot.params['dataCode'];
        this.dataName = this.route.snapshot.params['dataName'];
        this.iswork = this.route.snapshot.params['priceRuleExecuteCode'];
        //工作流
        if (this.route.snapshot.params['priceRuleExecuteCode']) {
            //查询
            this.httpService.selectPriceRuleExecuteInfo({ priceRuleExecuteCode: this.route.snapshot.params['priceRuleExecuteCode'] }).subscribe(data => {
                this.ruleexeconfig = data;
                this.conditionalDimensionList = data.conditionalDimensionList;
                if (this.conditionalDimensionList && this.conditionalDimensionList != null) {
                    for (let i = 0; i < this.conditionalDimensionList.length; i++) {
                        if (this.conditionalDimensionList[i].conditionalDimensionName.indexOf('日期') == -1 && this.conditionalDimensionList[i].conditionalDimensionCode != 'productId') {
                            this.selectProductCondValueList(this.conditionalDimensionList[i].conditionalDimensionCode, i);
                        }
                    }
                }
                this.rateName = data.rateName;
                for (let i = 0; i < this.conditionalDimensionList.length; i++) {
                    if (this.conditionalDimensionList[i].conditionalDimensionCode == 'productId') {
                        this.productPriceCode = this.conditionalDimensionList[i].dimensionValue;
                        this.httpService.findAvailableProductList({ productId: this.productPriceCode }).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.productPriceName = data.resultList[0].productName;
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: '调用服务失败！' });
                        })
                        continue;
                    }
                    this.chooseName.push({ "label": this.conditionalDimensionList[i].conditionalDimensionName, "value": this.conditionalDimensionList[i].conditionalDimensionCode });
                    this.chooseList.push(this.conditionalDimensionList[i].conditionalDimensionCode);
                    this.dimensionList.push(this.conditionalDimensionList[i].dimensionValue);
                }

            }, err => {
            })
        }
        //修改
        if (this.dataCode && this.dataCode != null) {
            this.selectPriceRuleExecuteInfo();
        }
        if (this.title == '规则执行配置新增') {
            this.ruleexeconfig.fromDate = this.commfunc.transDateN(this.minDate);
        }
        this.selectProductPriceActionTypeNameList();
        this.selectProductCondValue();
        this.codeVal();
        this.listOptions = [{ label: '基础利率', value: '1' }, { label: '基准利率', value: '3' }, { label: '市场利率', value: '4' }];//利率类型名称
        this.interestType = [{ label: '请选择', value: '' }, { label: '存款活期利率', value: '01' }, { label: '存款定期利率', value: '02' }, { label: '贷款利率', value: '03' }];//利率种类
        this.state = [{ label: '请选择', value: '' }, { label: '录入待复核', value: '0' }, { label: '复核生效', value: '1' }, { label: '修改待复核', value: '2' },
        { label: '作废待复核', value: '3' }, { label: '作废', value: '4' }, { label: '复核退回', value: '5' }];//状态
        this.baseRateInterestList = [{ label: '日利率', value: '01' }, { label: '月利率', value: '02' }, { label: '年利率', value: '03' }];
        this.productinterestrate.rateClassify = '1';
        setTimeout(() => {
            this.selectProductCondNameList();
        },0);
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //规则行为名称码值
    selectProductPriceActionTypeNameList() {
        let params = {
        }
        this.httpService.selectProductPriceActionTypeNameList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.productPriceActionTypeList.forEach((v) => {
                    List.push({ "label": v.priceRuleActionName, "value": v.priceRuleActionCode });
                })
                this.ActionTypeList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则行为名称查询失败', detail: '调用服务失败！' });
        })
    }
    //条件维度编号
    selectProductCondNameList() {
        let params = {
        }
        this.httpService.selectProductCondNameList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                data.enumerationTypeList.forEach((v) => {
                    List.push({ "label": v.conditionalDimensionName, "value": v.conditionalDimensionCode });
                })
                this.ProductCondList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '条件维度名称查询失败', detail: '调用服务失败！' });
        })
    }
    //条件维度名称
    selectProductCondValueList(item, i) {
        let params = {
            conditionalDimensionCode: item
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": '' });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.ProductValueList[i] = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询失败', detail: '调用服务失败！' });
        })
    }
    //期限码值
    selectProductCondValue() {
        let params = {
            conditionalDimensionCode: 'baseRateTerm'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.rateTermList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询失败', detail: '调用服务失败！' });
        })
    }
    //弹出利率弹框
    onChange() {
        if (this.chooseName.length > this.chooseList.length) {
            for (let i = 0; i < this.chooseName.length; i++) {
                let j = 0;
                for (j; j < this.chooseList.length; j++) {
                    if (this.chooseName[i].value == this.chooseList[j]) {
                        break;
                    }
                }
                if (j == this.chooseList.length) {
                    this.dimensionList.splice(i, 1);
                    break;
                }
            }
        }
        this.chooseName = [];
        this.ProductValueList = [];
        for (let i = 0; i < this.chooseList.length; i++) {
            this.ProductCondList.forEach(item => {
                if (item.value == this.chooseList[i]) {
                    this.chooseName.push({ "label": item.label, "value": item.value });
                }
            });
        }
        for (let i = 0; i < this.chooseName.length; i++) {
            this.selectProductCondValueList(this.chooseName[i].value, i);
        }
    }
    //弹出可售产品弹框
    chooseProduct() {
        this.displays = true;
        this.pquery();
    }
    //可售产品查询
    pquery() {
        this.pageSizes = 10;
        this.pageNums = 1;
        this.firsts = 0;
        this.getProductIdAndName();
    }
    //可售产品重置
    preset() {
        this.salableproduct = new SalableProduct();
        this.pquery();
    }
    //可售产品查询接口
    getProductIdAndName() {
        let params = {
            pageSize: this.pageSizes,
            pageNum: this.pageNums,
            productId: this.salableproduct.productId,
            productName: this.salableproduct.productName,
        }
        this.httpService.findAvailableProductList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productList = data.resultList;
                this.totals = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: '调用服务失败！' });
        })
    }
    //产品回调
    choose(car) {
        this.productPriceCode = car.productId;
        this.productPriceName = car.productName;
    }
    //可售产品弹框确定
    mainSeve() {
        this.displays = false;
    }
    //可售产品弹框取消
    goBack() {
        this.displays = false;
    }
    //弹出机构树弹框
    openOrgId(param) {
        this.orgTreeDisplay = true;
        this.display = false;
        this.ownerOrgInValue = 'adjustOrg'
    }
    //机构树弹框关闭
    onHideClose() {
        this.orgTreeDisplay = false;
        this.display = true;
    }
    //机构树回调
    treeShow(param) {
        this.orgTreeDisplay = false;
        this.display = true;
        this.productinterestrate.rateOrgId = param.orgId;
        this.rateOrgName = param.orgName;
    }
    //产品弹框翻页
    paginates(event) {
        this.pageSizes = event.rows * 1;
        //当前页
        this.pageNums = event.page + 1;
        this.firsts = event.page * this.pageSizes;
        this.getProductIdAndName();
    }
    //修改初始化
    selectPriceRuleExecuteInfo() {
        let params = {
            priceRuleExecuteName: this.dataName,//规则执行名称
            priceRuleExecuteCode: this.dataCode,//	规则执行编号
        }
        this.httpService.selectPriceRuleExecuteInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.ruleexeconfig = data;
                this.conditionalDimensionList = data.conditionalDimensionList;
                if (this.conditionalDimensionList && this.conditionalDimensionList != null) {
                    for (let i = 0; i < this.conditionalDimensionList.length; i++) {
                        if (this.conditionalDimensionList[i].conditionalDimensionName.indexOf('日期') == -1 && this.conditionalDimensionList[i].conditionalDimensionCode != 'productId') {
                            this.selectProductCondValueList(this.conditionalDimensionList[i].conditionalDimensionCode, i);
                        }
                    }
                }
                this.rateName = data.rateName;
                for (let i = 0; i < this.conditionalDimensionList.length; i++) {
                    if (this.conditionalDimensionList[i].conditionalDimensionCode == 'productId') {
                        this.productPriceCode = this.conditionalDimensionList[i].dimensionValue;
                        this.httpService.findAvailableProductList({ productId: this.productPriceCode }).subscribe(data => {
                            if (data.returnCode.code == 'success') {
                                this.productPriceName = data.resultList[0].productName;
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '可售产品查询失败', detail: '调用服务失败！' });
                        })
                        continue;
                    }
                    this.chooseName.push({ "label": this.conditionalDimensionList[i].conditionalDimensionName, "value": this.conditionalDimensionList[i].conditionalDimensionCode });
                    this.chooseList.push(this.conditionalDimensionList[i].conditionalDimensionCode);
                    this.dimensionList.push(this.conditionalDimensionList[i].dimensionValue);
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '规则执行信息查询成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则执行信息查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则执行信息查询失败', detail: '调用服务失败！' });
        })
    }
    //通过利率类型编号及名称查询利率编号和利率值
    selectRateIdAndRateCode(item) {
        let params = {
            rateTypeCode: item,	//	利率类型名称
        }
        this.httpService.selectRateIdAndRateCode(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.ruleexeconfig.priceRuleActionValue = data.productBaseRateValue;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '查询利率值成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询利率值失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询利率值失败', detail: '调用服务失败！' });
        })
    }
    //当规则行为名称不为“按基础利率替换”， 规则行为取值置空
    RuleActionValue() {
        if (this.ruleexeconfig.priceRuleActionCode == 'A1004' || this.ruleexeconfig.priceRuleActionCode == 'A1005' ||
            this.ruleexeconfig.priceRuleActionCode == 'A1006' || this.ruleexeconfig.priceRuleActionCode == 'A1007' ||
            this.ruleexeconfig.priceRuleActionCode == 'A1008') {
            this.display = true;
            if (this.ruleexeconfig.priceRuleActionCode == 'A1004') {
                this.productinterestrate.rateSort = '01';
            } else if (this.ruleexeconfig.priceRuleActionCode == 'A1005' || this.ruleexeconfig.priceRuleActionCode == 'A1006'
                || this.ruleexeconfig.priceRuleActionCode == 'A1007') {
                this.productinterestrate.rateSort = '02';
            } else if (this.ruleexeconfig.priceRuleActionCode == 'A1008') {
                this.productinterestrate.rateSort = '03';
            } else {
                this.productinterestrate.rateSort = '';
            }
            this.ruleexeconfig.priceRuleActionValue = null;
            this.numberShow = false;
            this.ruleexeconfig.productRateCode = '';
            this.rateName = '';
        } else {
            this.ruleexeconfig.priceRuleActionValue = null;
            this.numberShow = false;
            this.ruleexeconfig.productRateCode = '';
        }
        this.query();
        this.selectedValue = null;
    }
    //生、失效日期大小比较
    selectTime(a) {
        this.ruleexeconfig.fromDate = this.commfunc.transDateN(this.ruleexeconfig.fromDate);
        this.ruleexeconfig.thruDate = this.commfunc.transDateN(this.ruleexeconfig.thruDate);
        if (this.ruleexeconfig.fromDate && this.ruleexeconfig.thruDate) {
            if (this.ruleexeconfig.fromDate >= this.ruleexeconfig.thruDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期不能大于失效日期!' });
                if (a) {
                    setTimeout(() => {
                        this.ruleexeconfig.thruDate = null;
                    })
                } else {
                    setTimeout(() => {
                        this.ruleexeconfig.fromDate = null;
                    })
                }
            }
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.ruleexeconfig.priceRuleExecuteCode != '') {
            if (checkEnAndNum(null, this.ruleexeconfig.priceRuleExecuteCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else {
            this.EnAndNumShow = false;
        }
    }
    // 数字校验
    onNumberCheck() {
        if (this.ruleexeconfig.priceRuleActionValue != '') {
            if (TwoInteger(null, this.ruleexeconfig.priceRuleActionValue)) {
                this.numberShow = true;
            } else {
                this.numberShow = false;
            }
        } else {
            this.numberShow = false;
        }
    }
    onNumberCheck1() {
        if (this.productinterestrate.productBaseRateValue != '') {
            if (TwoInteger(null, this.productinterestrate.productBaseRateValue)) {
                this.numberShow1 = true;
            } else {
                this.numberShow1 = false;
            }
        } else {
            this.numberShow1 = false;
        }
    }
    //特殊字符校验
    onChineseCheck1() {
        if (this.ruleexeconfig.priceRuleExecuteName != '') {
            if (specialSymbols(null, this.ruleexeconfig.priceRuleExecuteName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else {
            this.ChineseShow = false;
        }
    }
    onChineseCheck2() {
        if (this.productinterestrate.rateName != '') {
            if (specialSymbols(null, this.productinterestrate.rateName)) {
                this.raterateNameShow = true;
            } else {
                this.raterateNameShow = false;
            }
        } else {
            this.raterateNameShow = false;
        }
    }
    //判断条件维度是否是日期输入框
    isdropdown(item) {
        if (item != null) {
            if (item.indexOf('日期') != -1) {
                return true;
            } else {
                return false;
            }
        }
    }
    //保存
    save() {
        this.isClick = true;
        let aa = [];
        for (let i = 0; i < this.chooseName.length; i++) {
            if(!this.dimensionList[i] || this.dimensionList[i]==null){
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.chooseName[i].label + '不能为空' });
                this.isClick = false;
                return;
            }
            if (this.chooseName[i].label.indexOf('日期') != -1) {
                this.dimensionList[i] = this.commfunc.transDateN(this.dimensionList[i])
            }
            aa.push({
                conditionalDimensionCode: this.chooseName[i].value, conditionalDimensionName: this.chooseName[i].label, operatorTypeId: '02', dimensionValue: this.dimensionList[i]
            });
        }
        if (this.productPriceCode && this.productPriceCode != null) {
            aa.push({
                conditionalDimensionCode: 'productId', conditionalDimensionName: '产品', operatorTypeId: '02', dimensionValue: this.productPriceCode
            });
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '产品编号不能为空' });
            this.isClick = false;
            return;
        }
        this.conditionalDimensionList = aa;
        //规则执行编号不能为空
        if (!this.ruleexeconfig.priceRuleExecuteCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则执行编号不能为空' });
            this.isClick = false;
            return;
        }
        //规则执行名称不能为空
        if (!this.ruleexeconfig.priceRuleExecuteName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则执行名称不能为空' });
            this.isClick = false;
            return;
        }
        //规则行为名称不能为空
        if (!this.ruleexeconfig.priceRuleActionCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则行为名称不能为空' });
            this.isClick = false;
            return;
        }
        //规则行为取值不能为空
        if (!this.ruleexeconfig.priceRuleActionValue) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则行为取值不能为空' });
            this.isClick = false;
            return;
        }
        //生效日期不能为空
        if (!this.ruleexeconfig.fromDate) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期不能为空' });
            this.isClick = false;
            return;
        }
        if (this.EnAndNumShow || this.ChineseShow || this.numberShow) {
            this.isClick = false;
            return;
        }
        if (this.title == '规则执行配置新增') {
            let params = {
                //ruleType: this.ruleType,
                priceRuleExecuteName: this.ruleexeconfig.priceRuleExecuteName,	//规则执行名称
                priceRuleExecuteCode: this.ruleexeconfig.priceRuleExecuteCode,	//	规则执行编号
                priceRuleActionCode: this.ruleexeconfig.priceRuleActionCode,	//	规则行为编号
                priceRuleActionName: this.ruleexeconfig.priceRuleActionName,	//	规则行为名称
                productRateCode: this.ruleexeconfig.productRateCode,
                priceRuleActionValue: this.ruleexeconfig.priceRuleActionValue,	//	规则行为取值
                fromDate: this.commfunc.transDateN(this.ruleexeconfig.fromDate),	//		生效日期
                thruDate: this.commfunc.transDateN(this.ruleexeconfig.thruDate),	//		失效日期
                conditionalDimensionList: this.conditionalDimensionList,	//	条件维度明细列表                
            }
            this.httpService.createPriceRuleExecuteInfo(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '规则执行配置新增成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-rule-exe-config', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '规则执行配置新增失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则执行配置新增失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        } else if (this.title == '规则执行配置修改') {
            let params = {
                //ruleType: this.ruleType,
                priceRuleExecuteName: this.ruleexeconfig.priceRuleExecuteName,	//规则执行名称
                priceRuleExecuteCode: this.ruleexeconfig.priceRuleExecuteCode,	//	规则执行编号
                priceRuleActionCode: this.ruleexeconfig.priceRuleActionCode,	//	规则行为编号
                priceRuleActionName: this.ruleexeconfig.priceRuleActionName,	//	规则行为名称
                productRateCode: this.ruleexeconfig.productRateCode,
                priceRuleActionValue: this.ruleexeconfig.priceRuleActionValue,	//	规则行为取值
                priceRuleActionSeqCode: this.ruleexeconfig.priceRuleActionSeqCode,	//	规则行为序列编号
                fromDate: this.commfunc.transDateN(this.ruleexeconfig.fromDate),	//		生效日期
                thruDate: this.commfunc.transDateN(this.ruleexeconfig.thruDate),	//		失效日期                
                conditionalDimensionList: this.conditionalDimensionList,	//	条件维度明细列表
                //处理工作流的参数
                // taskCategoryId: this.taskCategoryId + '',           
            }
            this.httpService.updatePriceRuleExecuteInfo(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '规则执行配置修改成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-rule-exe-config', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '规则执行配置修改失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则执行配置修改失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        }
    }
    //增加一条
    addBtn() {
        if (this.conditionalDimensionList == null) {
            this.conditionalDimensionList = [];
        }
        let aa = _.clone(this.conditionalDimensionList);
        aa.push({
            conditionalDimensionCode: '', conditionalDimensionName: '', operatorTypeId: '', dimensionValue: '', remarks: ''
        });
        this.conditionalDimensionList = aa;
    }
    //删除
    toDelete(i, col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                if (col.conditionalDimensionSeqCode && col.conditionalDimensionSeqCode != null) {
                    let params = {
                        conditionalDimensionSeqCode: col.conditionalDimensionSeqCode,
                        priceRuleExecuteCode: this.ruleexeconfig.priceRuleExecuteCode,
                    }
                    this.httpService.removeProductPriceCond(params).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '条件维度种类删除成功', detail: data.returnCode.message });
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: '调用服务失败！' });
                    })
                }
                for (let j = i; j < this.ProductValueList.length; j++) {
                    if (this.ProductValueList[j + 1]) {
                        this.ProductValueList[j] = this.ProductValueList[j + 1];
                    } else {
                        this.ProductValueList.splice(j, 1);
                    }
                }
                this.conditionalDimensionList = _.clone(this.conditionalDimensionList);
            }
        });
    }
    //返回
    cancel() {
        this.router.navigate(['/pages/tzb/valuation/rule/price-rule-exe-config', { query: '1' }])
    }
    //利率弹框翻页
    paginatees(event) {
        this.pageSizees = event.rows * 1;
        //当前页
        this.pageNumes = event.page + 1;
        this.firstes = event.page * this.pageSizees;
        this.selectRateTypeNameAndBaseRateList();
    }
    //利率类型名称及基础利率值查询
    query() {
        if (this.raterateNameShow || this.numberShow1) {
            return;
        }
        this.firstes = 0;
        this.pageSizees = 10;
        this.pageNumes = 1;
        this.selectRateTypeNameAndBaseRateList();
    }
    selectRateTypeNameAndBaseRateList() {
        let params = {
            rateName: this.productinterestrate.rateName,//	利率名称
            rateClassify: this.productinterestrate.rateClassify,//	利率分类类型
            rateSort: this.productinterestrate.rateSort,//	利率种类
            rateTerm: this.productinterestrate.rateTerm,//	期限
            orgId: this.productinterestrate.rateOrgId,//机构
            productBaseRateValue: this.productinterestrate.productBaseRateValue,//利率值
            statusCode: this.productinterestrate.statusCode,//状态
            pageSize: this.pageSizees,
            pageNum: this.pageNumes,
        }
        this.httpService.selectRateTypeNameAndBaseRateList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.rateTypeNameAndBaseRateList = data.rateTypeNameAndBaseRateList;
                this.totales = data.totalNum;
                for (let l = 0; l < this.rateTypeNameAndBaseRateList.length; l++) {
                    this.rateTypeNameAndBaseRateList[l].fromDate = this.commfunc.transDateN(this.rateTypeNameAndBaseRateList[l].fromDate);
                    this.rateTypeNameAndBaseRateList[l].thruDate = this.commfunc.transDateN(this.rateTypeNameAndBaseRateList[l].thruDate);
                    if (this.rateTypeNameAndBaseRateList[l].orgId && this.rateTypeNameAndBaseRateList[l].orgId != null) {
                        this.httpService.queryOrgById({ orgId: this.rateTypeNameAndBaseRateList[l].orgId }).subscribe(data => {//根据机构查询机构名
                            if (data.returnCode.code == 'success') {
                                this.rateTypeNameAndBaseRateList[l].rOrgName = data.orgName;
                            }
                            else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                        });
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率类型查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '利率类型查询失败', detail: '调用服务失败！' });
        })
    }
    //重置
    reset() {
        this.productinterestrate = new ProductInterestRate();
        this.query();
    }
    select(col) {
        if (this.ruleexeconfig.priceRuleActionCode == 'A1004' || this.ruleexeconfig.priceRuleActionCode == 'A1005' ||
            this.ruleexeconfig.priceRuleActionCode == 'A1006' || this.ruleexeconfig.priceRuleActionCode == 'A1007' ||
            this.ruleexeconfig.priceRuleActionCode == 'A1008') {
            this.ruleexeconfig.priceRuleActionValue = col.productBaseRateValue;
            this.ruleexeconfig.productRateCode = col.productRateCode;
            this.rateName = col.rateName;
        }
        this.display = false;
    }
}
